@media (min-width: 992px){
  .sidenavOne {
    overflow-y: hidden !important;
  }
  
  .sidenavOne:hover {
    overflow-y: auto !important;
  }
  .sidenav{
    width: 250px !important;
  }
}

@media (max-width: 992px){
  .sidenav{
    width: 200px !important;
  }
}
h1 {
    font-weight: 300;
  }
  
.table-of-contents a:hover{
    border-left: 2px solid #4a148c;
    font-weight: 500;
    
}

.table-of-contents a{
    height: 1.5rem;
    line-height: 1.5rem;
    font-size: 16px;
    
}


.table-of-contents a.active{
    border-left: 2px solid #4a148c;
    font-weight: 400;
}
.iframeStyle{
    width: 75%;
    height: 500px;
    border: 0;
    border-radius: 4px;
    overflow: hidden;
}

.spacingLeft{
    padding-left: 3%;
}

@media (max-width: 992px){
    .iframeStyle{
        width: 100%;
    }
    .spacingLeft{
        padding-left: 0%;
    }
}


